export default {
	data() {
		return {
			source: 0,
			addressList: [

			]
		}
	},
	onLoad(option) {
		this.refreshList()
		this.source = option.source;
	},
	methods: {
		//选择地址
		checkAddress(item) {
			if (this.source == 1) {
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().addressData = item
				this.$api.prePage().calcFreightPrice()
				uni.navigateBack()
			}
		},
		addAddress(type, item) {
			uni.navigateTo({
				url: `/pages/address/create?type=${type}&data=${JSON.stringify(item)}`
			})
		},
		//添加或修改成功之后回调
		refreshList(data, type) {
			const that = this
			that.$api.request('address', 'getAllAddress').then(res => {
				that.addressList = res.data
			})
		},
		deleteShow(item) {
			const that = this
			uni.showModal({
				title: '删除提示',
				content: '您确定要删除该地址吗？',
				showCancel: true,
				confirmText: '删除',
				success: (e) => {
					if (e.confirm) {
						that.$api.request('address', 'deleteAddress', {
							addressId: item.id
						}).then(res => {
							that.refreshList()
						})
					}
				},
				fail: () => {}
			})
		}
	}
}
